import { createTheme, responsiveFontSizes } from "@mui/material/styles";

const lightTheme = responsiveFontSizes(
  createTheme({
    palette: {
      mode: "light",

      primary: {        
        // light: "#135134",        
        main: "#344e87",        
        // dark: "#006661",        
      },
      secondary: {
        main: '#ffffff',
      },
      error: {
        main: "#e91e63",
      },
    },
    spacing: 4,
    direction: "rtl",
    shape: {
      borderRadius: 4,
    },
    typography: {
      fontFamily: "iransans",
    },
  })
);

const darkTheme = responsiveFontSizes(
  createTheme({
    palette: {
      mode: "dark",
    },
    spacing: 4,
    direction: "rtl",
    shape: {
      borderRadius: 4,
    },
    typography: {
      fontFamily: "Arial",
    },
  })
);

export { lightTheme, darkTheme };
